
import ClipboardJS from 'clipboard';
import axios from 'axios';

var mySwiper;

export default {
    name: 'Post',
    mounted() {

        var clipboard = new ClipboardJS('.invitecode');
        clipboard.on('success', (e) => {
            e.clearSelection();
            // clipboard.destroy();
            this.$util.doClipboardCallback();
        });

        this.getBanner();
        this.getPostDetail();
        this.getCountryCode();
        this.shareid = this.$util.getUrlParam('shareid');
    },
    data() {
        return {
            postData: '',
            comments: [],
            momentsData: '',
            countryCode: 'CN',
            picWidth: 0,
            picWidth66: 0,
            shareid: '',
            banner: '',
            videoReady: false,
            messages: {
                en: {
                    tweebaa: 'Tweebaa',
                    views: "Views",
                    likes: 'Likes',
                    comments: 'Comments',
                    topComments: 'Top Comments',
                    topPost: 'Top Post',
                    hi: "Hi,I'm ",
                    tips: 'You can also create great content',
                    downloadApp: 'Download Tweebaa App Get Tweebucks',
                    platform: 'Tweebaa is the world’s 1st multi-dimensional value-exchange social networking platform. Designed for creating value from non-traditional products, ideas, and your personal influence; Tweebaa allows anyone to earn on a large scale with zero financial investment. With the tools we provide, anyone cancreate a personalized foundation for their business completely free and suggest, shop, or share products with people all over the world and earn money.',
                },
                zh: {
                    tweebaa: '推易吧',
                    views: '浏览',
                    likes: '赞',
                    comments: '评论',
                    topComments: '热门留言',
                    topPost: '他（她）的热门瞬间',
                    hi: "Hi,我是 ",
                    tips: '你也能创造精彩瞬间',
                    downloadApp: '下载推易吧App，获取T豆奖励',
                    platform: '推易吧是一个让您利用自己的知识、经验、技巧、兴趣和人脉来变现的平台。推易吧会为您提供多样化的创意产品和工具，让您轻松开始零成本、零库存的创业之旅。',
                },
            }
        };
    },

    methods: {
        getBanner() {
            let i = Math.floor(Math.random() * 4) + 1;

            try {
                this.banner = require('../../image/banner' + i + '-' + this.$util.language + '.jpg');
            } catch (e) {

            }

        },
        getCountryCode() {
            var that = this;
            // $.getJSON("http://ip-api.com/json/?callback=?", function (data) {
            //     that.countryCode = data.countryCode;
            // });
            axios.get("https://api.ipdata.co/?api-key=78e1295abccb1616027b85a6bcb612d286aef4cdb397a2c091fd861a") // 使用eric.guo77@gmail.com 申请的code
                .then(function (response) {
                    console.log(response);
                    that.countryCode = response.data.country_code;
                })
                .catch(function (error) {
                    console.log(error);
                });
        },
        getPostDetail() {
            var that = this;
            var postId = this.$util.getUrlParam('postid');
            var url = this.$util.getBaseUrl() + "api/moments/" + postId;
            this.$util.getAxois(url, "GET", {}).then(function (response) {
                // console.log(JSON.stringify(response));
                that.postData = response.data;
                setTimeout(() => {
                    var div = document.getElementById("code");
                    div.dataset.clipboardText = that.shareid;
                    that.getComments(response.data.MomentGuid, response.data.Id);
                    that.getAllmonents(response.data.Owner.Id);
                    if (response.data.Videos.length > 0) {
                        // console.log(response.data.Videos[0].ThumbnailUrl);
                        playByWowzaPlayer(response.data.Videos[0].DownloadUrl, response.data.Videos[0].ThumbnailUrl);
                    }

                    mySwiper = new window.Swiper('.swiper-container', {
                        direction: 'horizontal',
                        loop: false,
                        initialSlide: 0,
                        pagination: {
                            el: '.swiper-pagination',
                            type: 'bullets'
                        }
                    });
                    document.getElementById('swiper').style.display = 'none';

                    var url = that.$util.getBaseUrl() + "api/v2/analytics";
                    var pram = {
                        AnalyticsItemTypeId: 1,
                        AnalyticsActionTypeId: 1,
                        PostId: postId,
                        CountryCode: that.countryCode,
                        StaySeconds: Math.floor(Math.random() * (120 - 10)) + 10
                    };
                    that.$util.getAxois(url, "POST", pram).then(function (response) {
                        //console.log(JSON.stringify(response.data));
                    }).catch(function (error) {
                        console.log(JSON.stringify(error) + '#######################');
                    });
                    that.picWidth = document.getElementById("testWidth").offsetWidth;
                    that.picWidth66 = document.getElementById("testWidth66").offsetWidth;
                });
            }).catch(function (error) {
                console.log(JSON.stringify(error));
            });

            function playByWowzaPlayer(url, img) {
                var myPlayer = window.WowzaPlayer.create("tweebaaPlayerElement", {
                    "license": that.$util.getL(),
                    "title": "",
                    "description": "",
                    "sourceURL": url,
                    "autoPlay": true,
                    "volume": "75",
                    "mute": false,
                    "loop": false,
                    "audioOnly": false,
                    "uiShowQuickRewind": true,
                    "uiQuickRewindSeconds": "15",
                    "posterFrameURL": img
                });

                function startBeachBall() {
                    console.log("Starting BeachBall");
                    // document.getElementById("loading").innerHTML = "<br /><i class='fa fa-spinner fa-spin'></i>";
                }

                function stopBeachBall() {
                    console.log("Stopping BeachBall");
                    document.getElementById("loading").innerHTML = "";
                }

                // Called when play requested
                function onPlayCallback(event) {

                    if (event.eventID === "onAssetPlayerStarted") {
                        startBeachBall();

                        // Listen for playback starting
                        myPlayer.onPlayheadTime(onPlayheadTimeCallback);
                    }
                }


                function onPlayheadTimeCallback(event) {
                    if (event.time > 0) {
                        // Video is streaming
                        stopBeachBall();

                        // Unregister as listener
                        myPlayer.removeOnPlayheadTime(onPlayheadTimeCallback);
                    }
                }

                that.videoReady = true;
                myPlayer.onPlay(onPlayCallback);
            }


        },
        openPic(index) {
            //console.log(index);
            mySwiper.slideTo(index);
            document.getElementById('swiper').style.display = 'flex';
        },
        hidePic() {
            document.getElementById('swiper').style.display = 'none';
        },
        getComments(momentGuid, momentId) {
            var that = this;
            var paramStr = {
                "PostGuid": momentGuid,
                "PostId": momentId
            };
            var url = this.$util.getBaseUrl() + "/api/v2/post/comments";
            this.$util.getAxois(url, "POST", paramStr).then(function (response) {
                //console.log(JSON.stringify(response.data));
                that.comments = response.data;
            }).catch(function (error) {
                console.log(JSON.stringify(error));
            });
        },
        getAllmonents(ownerId) {
            var that = this;
            var url = this.$util.getBaseUrl() + "/api/v2/moments";
            var paramStr = {
                "ownerId": ownerId,
                "currentpage": 0,
                "itemsperpage": 3,
                "ShowOnHomePage": true,
            };
            this.$util.getAxois(url, "POST", paramStr).then(function (response) {
                //console.log(JSON.stringify(response.data));
                that.momentsData = response.data.Data;
            }).catch(function (error) {
                console.log(JSON.stringify(error));
                //messages($.i18n.prop('systemError'), 'error', 'top', 3);
            });
        },
        gotoDownload() {
            document.getElementById("code").click();
            setTimeout(function () {
                if (getPhoneSystem() === 'ios') {
                    window.location.href = 'https://apps.apple.com/us/app/tweebaa/id1467778439';
                } else if (getPhoneSystem() === 'Android') {
                    window.location.href = 'weixin.html';
                }
            }, 1000)
        }
    }
}
